import { BiPlugin } from '../../../types';
import toolbar from './toolbar';
import { DEPRECATED_ACTIONS, actions } from '../actions';
import onAction from '../handlers/onAction';
import onKeyDown from '../handlers/onKeyDown';
import { createCommands } from '../commands';

function createClearStylePlugin(config): BiPlugin {
  const clearStyleConfig = config.clearStyle || {};

  return {
    toolbar: toolbar(clearStyleConfig),
    commands: createCommands(clearStyleConfig),
    onAction,
    actions,
    onKeyDown,
    models: [],
  };
}

createClearStylePlugin.actions = DEPRECATED_ACTIONS;

export default createClearStylePlugin;
